<template>
    <div class="custom-container">
      <div class="container-box">
  
        <div class="custom-box common-box">
          <div class="list-title">
            <div class="list-title-text">商户列表</div>
          </div>
          <!-- 商户列表 -->
          <div class="common-listcontent">
            <div
              class="custom-item common-item"
              v-for="(user, index) in users"
              :key="index"
              @click="showUserService(user, index)"
              :class="{ 'active': selectedIndex === index }"
            >
              <div class="user-name" :class="{ 'allopen': isshowDatalist }"><span style="color: #777777;">商户:</span><span>{{ user.username }}</span></div>
              <div class="user-info" @click.stop="deleteItem(user.username,index)"><img src="../assets/customericon/close.png" alt=""></div>
            </div>
            <div class="custom-add common-item" @click="showAddUser = true">
              <img class="add-icon" src="../assets/customericon/add.png" alt="" />
              <div class="add-text">添加商户账号</div>
            </div>
  
            <!-- 底部占空 -->
            <div class="spacebox"></div>
          </div>
  
          <!-- 添加用户弹窗 -->
          <div v-if="showAddUser" class="modal">
            <div class="modal-content">
              <h3>添加新商户</h3>
              <div class="form-group">
                <label>商户账号：</label>
                <input
                  v-model="newUser.username"
                  type="text"
                  placeholder="请输入账号"
                  @keyup.enter="addUser"
                />
              </div>
              <div class="form-group">
                <label>密码：</label>
                <input
                  v-model="newUser.password"
                  type="password"
                  placeholder="请输入密码"
                  @keyup.enter="addUser"
                />
              </div>
              <div class="error-message" v-if="errorMessage">{{ errorMessage }}</div>
              <div class="modal-actions">
                <button
                  @click="addUser"
                  :disabled="!isFormValid"
                  class="confirm-btn"
                >
                  登录
                </button>
                <button @click="showAddUser = false" class="cancel-btn">
                  取消
                </button>
              </div>
            </div>
          </div>
        </div>
        <!-- 客服列表 -->
        <div class="service-box common-box" v-show="isshowService">
          <div class="list-title">
            <div class="list-title-text">客服列表</div>
  
            <button
              class="close-btn"
              @click="
                isshowService = false;
                isshowDatalist = false;
                selectedIndex= -1;
              "
            >
              <img src="../assets/customericon/close.png" alt="">
            </button>
          </div>
  
          <div class="searchbox">
            <input class="search-input" type="search" placeholder="请输入客服账号" v-model="searchText" />
            <button class="search-btn" @click="searchService">
              <img src="../assets/customericon/search.png" alt="">
            </button>
          </div>
  
          <div class="common-listcontent">
            <div class="common-item" v-for="(item,index) in serviceList" :key="item.id"  :class="{'active': selectedserviceIndex === index}">
              
              <div class="serviceMsgNum" v-show=" item.wp2msg + item.fsbmsg + item.otrmsg ">
                {{ item.wp2msg + item.fsbmsg + item.otrmsg > 99 ? "99+" : item.wp2msg + item.fsbmsg + item.otrmsg }}
              </div>
  
              <div class="servicePersonMsg">
  
                <!-- <div class="service-status">
                  <span
                    class="status-dot"
                    :class="{ active: item.status === 'online' }"
                  ></span>
                </div> -->
  
                <div class="service-name">{{"客服" + item.id + ":" + item.name }}</div>
                <div class="service-num">登录账号:{{ item.servicenum }}</div>
                
              </div>
              <!-- 平台按钮 -->
              <div class="service-info">
                <button class="station-btn" @click="showUserDetail('wp',index)"  v-show="item.wp2num" :class="{'activeTag': activeTab === 'wp' && selectedserviceIndex === index}">
                  <img class="station-icon" src="../assets/customericon/whatsapp.png" alt="">
                  <div class="station-name"><span v-show="!isshowDatalist">whatsapp</span>：{{ item.wp2num }}</div>
                  <div class="station-msg" v-show="item.wp2msg">{{ item.wp2msg > 99 ? "99+" : item.wp2msg}}</div>
                </button>
                <button class="station-btn" @click="showUserDetail('fb',index)" v-show="item.facebooknum" :class="{'activeTag': activeTab === 'fb' && selectedserviceIndex === index}">
                  <img class="station-icon" src="../assets/customericon/facebook.png" alt="">
                  <div class="station-name"><span v-show="!isshowDatalist">facebook</span>：{{ item.facebooknum }}</div>
                  <div class="station-msg" v-show="item.fsbmsg">{{ item.fsbmsg > 99 ? "99+" : item.fsbmsg}}</div>
                </button>
                <button class="station-btn" @click="showUserDetail('ot',index)" v-show="item.othernum" :class="{'activeTag': activeTab === 'ot' && selectedserviceIndex === index}">
                  <img class="station-icon" src="../assets/customericon/other.png" alt="">
                  <div class="station-name"><span v-show="!isshowDatalist">other</span>：{{ item.othernum }}</div>
                  <div class="station-msg" v-show="item.otrmsg">{{ item.otrmsg > 99 ? "99+" : item.otrmsg}}</div>
                </button>
              </div>
            </div>
            <!-- 底部占空 -->
            <div class="spacebox"></div>
          </div>
        </div>
        <div class="service-box common-box" v-show="!isshowService">
          <div class="nodata-space">
            <img src="../assets/customericon/no_data.png" alt="" />
          </div>
  
  
        </div>
  
        <!-- 客服列表下的平台账号列表 -->
        <div class="data-box common-box" v-show="isshowDatalist">
          <div class="list-title">
            <div class="list-title-lefttext">客服{{ selectedserviceIndex+1 }}:</div>
            <div class="list-title-text">{{ stationTitle }}</div>
            <button class="adddata-btn">
              <img src="../assets/customericon/addid.png" alt="">
            </button>
            
            <button 
              class="close-btn" 
              @click="
                isshowDatalist = false;
                selectedserviceIndex = -1;
              "
            >            
              <img src="../assets/customericon/close.png" alt="">
            </button>
          </div>
  
          <div class="searchbox">
            <input class="search-input" type="search" placeholder="请输入平台账号" v-model="searchDataText" />
            <button class="search-btn" @click="searchDataService">
              <img src="../assets/customericon/search.png" alt="">
            </button>
          </div>
  
          <div class="common-listcontent">
            <div
              class="data-item"
              v-for="(item, index) in paginatedItems"
              :key="index"
            >
              <div class="data-checkbox" v-show="isshowDataCheckbox">
                <input type="checkbox" v-model="item.selected" :id="'checkbox-' + index"/> 
              </div>
              <div class="data-img"><img src="../assets/customericon/no-image.png" alt=""></div>
              <div class="data-card">
                <div class="data-cardtitle">
                  <div class="data-name">账号:{{ item.name }}</div>
                  <div class="data-date">{{ item.date }}</div>
                </div>
                <div class="data-cardbuttom">
                  <div class="data-info">{{ item.content }}</div>
                  <div class="serviceMsgNum" v-show="item.msgnum">{{ item.msgnum > 99 ? "99+" : item.msgnum}}</div>
                </div>
              </div>
            </div>
            <div class="data-item"></div>
            <div class="data-buttom-button">
              <!-- 分页控制器 -->
              <div class="pagination-controls">
  
                <div class="pagination-controls-item align-center-box" v-show="!isshowDataCheckbox">
                  <div class="deal-box">
                    <button class="deal-box-btn" @click="isshowDataCheckbox = true">管理</button>
                  </div>
                  <div class="page-info-btn">
                    <button
                    @click="prevPage"
                    :disabled="currentPage === 1"
                    class="page-btn"
                  >
                    上一页
                    </button>
  
                    <div class="page-info">{{ currentPage }} / {{ totalPages }}</div>
  
                    <button
                      @click="nextPage"
                      :disabled="currentPage === totalPages"
                      class="page-btn"
                    >
                      下一页
                    </button>
                  </div>
                  <div class="deal-box"></div>
                </div>
  
                <!-- 勾选框操作 -->
                <div class="pagination-controls-item-next align-center-box" v-show="isshowDataCheckbox">
                  <button class="deal-box-btn">
                    <div class="data-checkbox">
                      <input 
                        type="checkbox" 
                        v-model="selectAll"
                        @change="toggleSelectAll"
                      >
                    </div>
                    全选</button>
                  
                  <button class="deal-box-btn" @click="deleteSelected">删除</button>
                  <button class="deal-box-btn"  @click="cancelSelectAll">取消</button>
                </div>
  
                
  
              </div>
            </div>
          </div>
  
  
        </div>
      </div>
    </div>
  </template>
    
    <script>
  export default {
    name: "CustomerList",
    data() {
      return {
        users: [], // 用户列表
        newUser: {
          username: "", // 新用户名
          password: "", // 新密码
        },
        errorMessage: "", // 错误信息
        stationTitle: "", // 站点标题
        searchText: "", // 搜索客服账号文本
        searchDataText: "", // 搜索平台账号文本
        selectedIndex: -1, // 记录选中项的索引
        selectedserviceIndex: -1, // 记录选中客服项的索引
        activeTab: "", // 当前激活的data标签页
  
        showAddUser: false, //显示新增用户弹窗（登录验证窗口）
        
        isshowService: false, //显示客服员列表
        isshowDatalist: false, //显示平台账号数据列表
        isshowDataCheckbox: false, //显示平台账号数据列表复选框
        //分页
        items: Array.from({ length: 105 }, (_, i) => ({
          id: i + 1,
          name:  `${Math.floor(Math.random() * 100000) + 1}`,
          content: ` 最近内容 ${i + 1}`,
          msgnum: Math.floor(Math.random() * 10) + 1,
          date: new Date().toLocaleDateString(),
          selected: false,
        })), // 模拟105条数据
  
        currentPage: 1,
        pageSize: 10,
        itemsTenIndex: [],   // 独立存储分页数据（是否需要目前存疑）
      
  
        serviceList: [],  //客服员列表
        serviceData1: [  //模拟数据-客服员列表1
          {
            id: 1,
            name: "张三",
            status: "online",
            facebooknum: 300,
            wp2num: 400,
            othernum: 1000,
            servicenum: 12324252627,
            wp2msg:20,
            fsbmsg: 30,
            otrmsg: 40,
          },
          {
            id: 2,
            name: "李四",
            status: "offline",
            facebooknum: 1,
            wp2num: 2,
            othernum: 0,
            servicenum: 12324252627,
            wp2msg:20,
            fsbmsg: 30,
            otrmsg: 0,
          },{
            id: 3,
            name: "王五",
            status: "online",
            facebooknum: 5,
            wp2num: 0,
            othernum: 5,
            servicenum: 12324252627,
            wp2msg:0,
            fsbmsg: 30,
            otrmsg: 40,
          },
        ],
        serviceData2: [  //模拟数据-客服员列表2
        {
            id: 1,
            name: "张三丰登",
            status: "online",
            facebooknum: 30,
            wp2num: 40,
            othernum: 100,
            servicenum: 12324252627,
            wp2msg:2,
            fsbmsg: 3,
            otrmsg: 4,
          },
          {
            id: 2,
            name: "李四郎",
            status: "online",
            facebooknum: 12,
            wp2num: 21,
            othernum: 33,
            servicenum: 12324252627,
            wp2msg:2,
            fsbmsg: 12,
            otrmsg: 21,
          },{
            id: 3,
            name: "王老五",
            status: "online",
            facebooknum: 5,
            wp2num: 0,
            othernum: 5,
            servicenum: 12324252627,
            wp2msg:0,
            fsbmsg: 30,
            otrmsg: 40,
          },{
            id: 4,
            name: "赵六",
            status: "online",
            facebooknum: 11,
            wp2num: 22,
            othernum: 33,
            servicenum: 12324252627,
            wp2msg:2,
            fsbmsg: 13,
            otrmsg: 5,
          },
          {
            id: 5,
            name: "胡七",
            status: "offline",
            facebooknum: 5,
            wp2num: 21,
            othernum: 0,
            servicenum: 12324252627,
            wp2msg:23,
            fsbmsg: 34,
            otrmsg: 0,
          },{
            id: 6,
            name: "朱八",
            status: "online",
            facebooknum: 0,
            wp2num: 10,
            othernum: 115,
            servicenum: 12324252627,
            wp2msg: 6,
            fsbmsg: 0,
            otrmsg: 18,
          },{
            id: 7,
            name: "刘九",
            status: "online",
            facebooknum: 128,
            wp2num: 256,
            othernum: 1024,
            servicenum: 12324252627,
            wp2msg: 66,
            fsbmsg: 38,
            otrmsg: 512,
          },
          {
            id: 8,
            name: "陈十",
            status: "offline",
            facebooknum: 1,
            wp2num: 2,
            othernum: 0,
            servicenum: 12324252627,
            wp2msg:20,
            fsbmsg: 30,
            otrmsg: 0,
          },{
            id: 9,
            name: "戚十一",
            status: "online",
            facebooknum: 5,
            wp2num: 0,
            othernum: 5,
            servicenum: 12324252627,
            wp2msg:0,
            fsbmsg: 30,
            otrmsg: 40,
          },
        ],
      };
    },
    created() {
      this.loadUsers();
    },
    computed: {
      //验证密码长度
      isFormValid() { 
        return (
          this.newUser.username.trim() !== "" && this.newUser.password.length >= 1
        );
      },
      
      // 当前页显示的数据
      paginatedItems() {
        const start = (this.currentPage - 1) * this.pageSize;
        const end = start + this.pageSize;
        return this.items.slice(start, end);
      },
  
      // 总页数
      totalPages() {
        return Math.ceil(this.items.length / this.pageSize);
      },
      selectAll: {
        get() {
          return this.items.length > 0 && 
                this.items.every(item => item.selected)
        },
        set(value) {
          
          //全选中状态
          this.items.forEach(item => {
            item.selected = value
          })
  
          // //当前分页的选中状态
          // this.itemsTenIndex.forEach(item => {
          //   item.selected = value
          // })
          // // 提取 B 的所有 id
          // const idsInB = new Set(this.itemsTenIndex.map(b => b.id));
          
          // // 创建新数组并设置 selected 属性
          // return this.items.map(aItem => {
          //   // 返回新对象，避免直接修改原数组
          //   return {
          //     ...aItem,          // 保留原数组项的所有属性
          //     selected: idsInB.has(aItem.id)  // 根据条件设置 selected
          //   };
          // });
          
        }
      },
    },
    watch: {
      currentPage() {
        this.syncItemsTenIndex();
      },
      pageSize() {
        this.syncItemsTenIndex();
      },
      
    },
    methods: {
      //当前页账号数据
      syncItemsTenIndex() {
        this.itemsTenIndex = [...this.paginatedItems]; // 创建新数组避免引用问题
        // console.log(this.itemsTenIndex);
      },
      // 加载本地存储的用户
      loadUsers() {
        const savedUsers = localStorage.getItem("users");
        if (savedUsers) {
          this.users = JSON.parse(savedUsers);
        }
      },
      // 验证用户名唯一性（自动去除首尾空格）
      isUsernameUnique(username) {
        const cleanUsername = username.trim();
        return !this.users.some(user => 
          user.username.trim() === cleanUsername
        );
      },
  
      // 添加用户
      addUser() {
        //基本校验
        if (!this.newUser.username.trim()) {
          this.errorMessage = '用户名不能为空！';
          return;
        }
   
        //唯一性验证
        if (this.isUsernameUnique(this.newUser.username)) {
          // 3. 通过验证后添加用户到数组头部
          this.users.push({
            username: this.newUser.username.trim(), // 存储时也去除空格
            password: this.newUser.password
          });
          this.saveUsers(); // 保存到本地存储
   
          //清空输入框和错误信息
          this.newUser.username = '';
          this.newUser.password = '';
          this.errorMessage = '';
          // if(this.selectedIndex!= -1) this.selectedIndex++;
          this.showAddUser = false; // 关闭添加用户表单
        } else {
          //显示重复错误
          this.errorMessage = `用户名 "${this.newUser.username.trim()}" 已存在！`;
        }
  
      },
      // 删除指定项
      deleteItem(name,index) {
        let indexNum ;
        if(index==this.selectedIndex){
          indexNum=-1;
          this.isshowService = false;
          this.isshowDatalist = false;
        }else if(index<this.selectedIndex){
          indexNum = this.selectedIndex - 1;
        }else if(index>this.selectedIndex){
          indexNum = this.selectedIndex;
        }
        // 过滤出需要保留的项（排除要删除的项）
        const updatedList = this.users.filter(item => item.username !== name);
        
        // 更新本地缓存
        localStorage.setItem('users', JSON.stringify(updatedList));
        
        // 更新响应式数据（Vue 会自动检测数组变化）
        this.users = updatedList;
  
        this.selectedIndex = indexNum;
      },
      // 保存到本地存储
      saveUsers() {
        localStorage.setItem("users", JSON.stringify(this.users));
      },
      // 显示客服员列表
      showUserService(user, index) {
        if (user) {
          this.selectedIndex = index; // 更新选中状态
          this.isshowService = true;
          this.isshowDatalist = false;
          this.selectedserviceIndex = -1 ;
  
          if (index !== 0 && index % 2 !== 0) {
            this.serviceList = this.serviceData1; // this.serviceList = this.serviceData.filter(item => item.id === user.id);
          } else {
            this.serviceList = this.serviceData2; //除了第一个index==0，其他都显示serviceData2
          }
        } else {
          this.isshowService = false;
        }
      },
      // 显示平台登录账号列表
      showUserDetail(station, index) {
        this.isshowDatalist = true;
        this.selectedserviceIndex = index;
        if (station === "wp") {
          this.stationTitle = "whatsapp账号";  //平台账号标题
          this.activeTab = "wp";               //高亮标签
        } else if (station === "fb") {
          this.stationTitle = "facebook账号";
          this.activeTab = "fb";
        }else if (station === "ot") {
          this.stationTitle = "其他平台账号";
          this.activeTab = "ot";
        } else {
          this.isshowDatalist = false;
        }
        this.syncItemsTenIndex();  //模拟接口赋值
        this.isshowDataCheckbox = false; //隐藏复选框
      },
      // 搜索框
      searchService(){
        // return this.serviceList.filter(item => item.serviceName.toLowerCase().includes(this.searchText.toLowerCase()))
        return "搜索结果";
      },
      searchDataService(){
        return "搜索结果";
      },
      //全选选中项
      toggleSelectAll(event) {
        this.selectAll = event.target.checked;
  
      },
      cancelSelectAll(){
        this.isshowDataCheckbox = false;
        this.items.forEach(item => {
          item.selected = false
        })
      },
      // 删除选中项
      deleteSelected() {
        this.items = this.items.filter(item => !item.selected) 
        //发送删除请求
      },
      // 上一页
      prevPage() {
        if (this.currentPage > 1) {
          this.currentPage--;
        }
      },
  
      // 下一页
      nextPage() {
        if (this.currentPage < this.totalPages) {
          this.currentPage++;
        }
      },
      
    },
  };
  </script>
    
    <style scoped>
  /* 公共容器样式 */
  .common-box {
    background: rgba(255, 255, 255, 0.95);
    border: 1px solid #ccc;
    /* border-radius: 16px; */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    /* width: 30vw; */
    /* max-width: 400px; */
    height: 80vh;
    transition: all 0.3s ease;
  }
  
  /* 公共列表项样式 */
  .common-item {
    height: 60px;
    border: 1px solid #ccc;
    border-radius: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    margin: 10px 5px;
    overflow: hidden;
    position: relative;
  }
  .spacebox {
    width: 100%;
    height: 60px;
  }
  .active {
    background-color: rgb(236, 238, 246);  /* 高亮背景色 */
    /* 可以添加其他高亮样式 */
    /* box-shadow: 0 2px 8px rgba(0,0,0,0.1); */
  }
  .activeTag{
    background-color: rgb(185, 212, 255); 
  }
  /* 外层容器样式  */
  .container-box {
    border-radius: 16px;
    width: 800px;
    overflow: hidden;
    border: 1px solid #a4a4a4;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .custom-container {
    
    min-height: 85vh;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .custom-box {
    /* 保留原有特定样式 */
    width: 100%;
    flex: 1;
  }
  
  .service-box {
    /* 保留原有特定样式 */
    flex: 2;
  
  }
  
  .data-box {
    /* 保留原有特定样式 */
    flex: 2;
  
  }
  /* 商户列表 */
  .user-name {
    font-size: 16px;
    font-weight: bold;
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    display: flex;
    justify-items: flex-start;
    align-items: center;
  }
  .allopen{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow: hidden;
  }
  .user-info {
    font-size: 28px;
    color: #666;
    position: absolute;
    top: 50%;
    right: 5%;
    transform: translateY(-50%);
    display: flex;
    justify-items: flex-end;
    align-items: center;
  }
  .user-info img{
    width: 15px;
    height: 15px;
  
  }
  
  .common-listcontent {
    width: 100%;
    height: 94%;
    overflow: auto;
    background: rgb(250, 251, 255);
    position: relative;
    /* border: 1px solid #a4a4a4; */
  }
  
